import React from "react";
import { useState } from "react";
import Item from "./Item";
import { v4 as uuidv4 } from "uuid";

const Form = () => {
  const [dataArr, setDataArr] = useState([
    { txt: "promener le chien", id: uuidv4() },
    { txt: "danser", id: uuidv4() },
    { txt: "faire les courses", id: uuidv4() },
    { txt: "coder", id: uuidv4() },
  ]);

  const [stateInput, setStateInput] = useState();

  const deleteElement = (id) => {
    //console.log(id);
    const filterState = dataArr.filter((item) => {
      return item.id !== id;
    });
    setDataArr(filterState);
  };

  const linkedInput = (e) => {
    setStateInput(e);
  };

  const addTodo = (e) => {
    e.preventDefault();
    const newArr = [...dataArr];

    const newTodo = {};
    newTodo.txt = stateInput;
    newTodo.id = uuidv4();

    newArr.push(newTodo);
    setDataArr(newArr);
    setStateInput("");
  };

  return (
    <div className="m-auto px-4 col-12 col-sm-10 col-lg-6">
      <form className="mb-3" action="" onSubmit={(e) => addTodo(e)}>
        <label htmlFor="todo" className="form-label mt-3">
          chose à faire ...
        </label>
        <input
          value={stateInput}
          id="todo"
          type="text"
          className="form-control"
          onInput={(e) => linkedInput(e.target.value)}
        />
        <button className="mt-2 btn btn-primary d-block">Envoyez</button>
      </form>

      <h2>Listes des choses à faire :</h2>
      <ul className="list-group">
        {dataArr.map((item) => {
          return (
            <Item
              txt={item.txt}
              key={item.id}
              id={item.id}
              delFunc={deleteElement}
            />
          );
        })}
      </ul>
    </div>
  );
};

export default Form;
